@import 'colors';
@import './header/main.scss';
@import './sections/headliner.scss';

*,*:before,*:after {
    box-sizing: border-box;
    font-family: inherit;
}

html,body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
    background: transparent;
}

main {
    height: 100%;
    width: 100%;
    overflow: scroll;
}

section {
    position: relative;
    height: 100%;
    width: 100%;
}

.logo {
    display: none;
    position: absolute;
    // display: inline-block;
    :hover {
        background: linear-gradient($logoGold,$logoBronze,$logoBlue);
    }
}

.services {
    position: relative;
    width: 100%;
    height: 100%;

    &__panels {
        justify-content: center;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        width: 100%;
    }
}

.services__panels--panel {
    --width: 380px;
    position: relative;
    height: 280px;
    width: var(--width);
    border-radius: 20px 48px 12px 32px;
    background-color: $primaryBlue;
    box-shadow: rgba(50,50,93,.25) 0 13px 27px -5px,rgba(0,0,0,.3) 0 8px 16px -8px,rgba(50,50,93,.25) 0 13px 27px -5px,rgba(0,0,0,.3) 0 8px 16px -8px;
}

@media screen and (min-width: 1200) {
    .container {
        max-width: 1140px;
    }
}

@media screen and (min-width: 1024px ) and (max-width: 1198px) {
    .container {
        max-width: 1024px;
    }
    
}

@media screen and (min-width: 1024px) {
    .services__panels--panel {
        --width: 25%;
    }
}


@media screen and (min-width: 768px) and (max-width: 1024px) {

    .services__panels--panel {
        --width: 380px;
    }
}

@media (min-width: 768px) {
    .container {
        padding-top: 80px;
        padding-right: 25px;
        padding-bottom: 0px;
        padding-left: 25px;

    }
}

@media screen and (max-width:767px) {
    .container {
        padding-top: 60px;
        padding-right: 25px;
        padding-bottom: 0px;
        padding-left: 25px;

    }
    .services__panels--panel {
        --width: 100%;
    }
}